class UtilConverter {
  static number2commadStringWithIntl(number) {
    try {
      var formatter = new Intl.NumberFormat("en-US");
      return formatter.format(number);
    } catch (e) {
      return number;
    }
  }

  static number2shortenString(number) {
    if (number >= 1000000000)
      return (Math.floor(number * 0.0000001) * 0.01).toFixed(2) + 'B';

    if (number >= 1000000)
      return (Math.floor(number * 0.0001) * 0.01).toFixed(2) + 'M';
  }
}
export default UtilConverter;