import React from 'react';
import { BrowserRouter, NavLink } from 'react-router-dom';
import { UtilCommon } from '../utils';
import { AppState, app_state_default } from './contexts';
import MainRoutes from './routers/main_routes';
import { ModalWrapper } from './modals';

import api from '../api/api';

function App() {
  const reducer = (prev_state, new_state) => ({ ...prev_state, ...new_state });
  const [state, setState] = React.useReducer(reducer, app_state_default);
  const [message_modals, setMessageModals] = React.useState([
    // <ModalAlert />
  ]);
  const [isServerOn, setIsServerOn] = React.useState(false);

  async function checkServer() {
    try {
      await api.get('/util/check')

      setIsServerOn(true);
    } catch (e) {
      console.log('App.checkServer', e);
      setIsServerOn(false);
    }
  }


  React.useEffect(() => {
    checkServer();
  }, []);


  // Functions for message_modals >>>>>>>>>>>>>>>>>>
  async function closeMessageModal(e) {
    setMessageModals(p_message_modals => {
      if (!p_message_modals.length)
        return p_message_modals;

      let n_message_modals = p_message_modals.slice();
      n_message_modals.shift();      
      return n_message_modals;  
    });

    if (!message_modals.length) {
      await UtilCommon.delay(200); // close animation time
      (document.getElementsByTagName('body')[0]).classList.remove('overflow-hidden');
    }
  }

  async function pushMessageModal({Modal, props = {}, onConfirm = () => {}, onCancel = () => {}}) {
    return new Promise((resolve, reject) => {
      if (!message_modals.length) {
        const body = document.getElementsByTagName('body')[0];
        body.classList.add('overflow-hidden');
      }

      setMessageModals(p_message_modals => {
        let n_message_modals = p_message_modals.slice();
        n_message_modals.push((
          <Modal
            {...props}
            onConfirm={async e => {
              await onConfirm();
              await closeMessageModal();
              resolve(true);
            }}
            onCancel={async e => {
              await onCancel();
              await closeMessageModal();
              resolve(false);
            }}
          />
        ));
        return n_message_modals;
      });
    });    
  }
  // <<<<<<<<<<<<<<<< Functions for message_modals

  return (
    <BrowserRouter>
      <AppState.Provider value={Object.assign({}, state, {      
        setState,
        pushMessageModal,
      })}>
        <React.Fragment>
          <ModalWrapper modals={message_modals} closeCurrentModal={closeMessageModal} />
          <nav className="d-block d-sm-none position-fixed sticky-top w-100">
            <div className="navbar navbar-dark bg-dark">
              <div className="container-fluid">
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarToggleExternalContent" aria-controls="navbarToggleExternalContent" aria-expanded="false" aria-label="Toggle navigation">
                  <span className="navbar-toggler-icon"></span>
                </button>
              </div>
            </div>
            
            <div className="collapse bg-dark" id="navbarToggleExternalContent">
              <ul className="nav nav-pills flex-column mb-auto fw-bold" style={{ minHeight: `${100}vh` }}>
                {isServerOn && (
                  <React.Fragment>
                    <li><NavLink to="/auth/login" className="nav-link fw-bold"></NavLink></li>
                    <li><NavLink to="/auth/register" className="nav-link fw-bold"></NavLink></li>
                  </React.Fragment>
                )}
                
                  <li><NavLink to="/d3/bubbles" className="nav-link fw-bold">D3</NavLink></li>
                <li className="ps-2"><NavLink to="/d3/bubbles" className="nav-link fw-bold" style={{ fontSize: `${0.875}rem`}}>Bubbles</NavLink></li>
                <li><NavLink to="/youtube-search" className="nav-link fw-bold">Youtube Search</NavLink></li>
                <li><NavLink to="/trade-history" className="nav-link fw-bold">Trade History</NavLink></li>
                <li><NavLink to="/day-trades" className="nav-link fw-bold">Day Trades</NavLink></li>
                <li><NavLink to="/" className="nav-link fw-bold">TEST</NavLink></li>
              </ul>
            </div>
          </nav>
          <nav className="d-block d-sm-none" style={{height: 50}}></nav>

          <nav className="d-none d-sm-inline-block position-fixed top-0 end-0 bg-dark" style={{zIndex: 1030, borderRadius: '0px 0px 0px 16px'}} >
            <ul className="nav d-flex justify-content-end fw-bold">
              {isServerOn && (
                <React.Fragment>
                  <li><NavLink to="/auth/login" className="nav-link fw-bold"></NavLink></li>
                  <li><NavLink to="/auth/register" className="nav-link fw-bold"></NavLink></li>                  
                </React.Fragment>
              )}                    
              <li><NavLink to="/d3/bubbles" className="nav-link fw-bold ps-4" style={{ fontSize: `${0.875}rem`}}>Bubbles</NavLink></li>
              <li><NavLink to="/youtube-search" className="nav-link fw-bold">Youtube Search</NavLink></li>
              <li><NavLink to="/trade-history" className="nav-link fw-bold">Trade History</NavLink></li>
              <li><NavLink to="/day-trades" className="nav-link fw-bold">Day Trades</NavLink></li>
              <li><NavLink exact to="/" className="nav-link fw-bold">TEST</NavLink></li>
            </ul>
          </nav>

          <MainRoutes />
        </React.Fragment>
      </AppState.Provider>
    </BrowserRouter>
  );
}

export default App;
