import React from "react";
import { Area, AreaChart, CartesianGrid, LabelList, Legend, ReferenceArea, ResponsiveContainer, Tooltip, XAxis, YAxis } from "recharts";
import { UtilCommon, UtilConverter } from "../../../utils";

const _colors_map = {
  "KOSPI"     : UtilCommon.lightenDarkenColor(UtilCommon.makeRandomColor(), -50),
  "Won"       : UtilCommon.lightenDarkenColor(UtilCommon.makeRandomColor(), -50),
  "Dollar"    : UtilCommon.lightenDarkenColor(UtilCommon.makeRandomColor(), -50),
  "Luxuary"   : UtilCommon.lightenDarkenColor(UtilCommon.makeRandomColor(), -50),
  "NASDAQ"    : UtilCommon.lightenDarkenColor(UtilCommon.makeRandomColor(), -50),
  "Reits"     : UtilCommon.lightenDarkenColor(UtilCommon.makeRandomColor(), -50),
  "S&P500"    : UtilCommon.lightenDarkenColor(UtilCommon.makeRandomColor(), -50),
  "Stocks-KR" : UtilCommon.lightenDarkenColor(UtilCommon.makeRandomColor(), -50),
  "Stocks-US" : UtilCommon.lightenDarkenColor(UtilCommon.makeRandomColor(), -50),
  "T-bond_I"  : UtilCommon.lightenDarkenColor(UtilCommon.makeRandomColor(), -50),
  "P-Metal"   : UtilCommon.lightenDarkenColor(UtilCommon.makeRandomColor(), -50),
};


const RechartTotalAsstRatio = (props) => {
  const { data }  = props;

  const [_viewPort, _setViewPort] = React.useState({left: '', right: ''});
  const [_purified_data, _setPurifiedData] = React.useState(data);

  const _reviseData = React.useCallback((from, to) => {
    if (!data || !data.length)
      return;

    let purified_data = [];

    let is_selected_range = false;
    for (let datum of data) {
      if (datum['date'] === from)
        is_selected_range = true;

      if (!is_selected_range)
        continue;
      
      purified_data.push(datum);

      if (datum['date'] === to)
        break;
    }

    return purified_data;
  }, [data]);

  React.useEffect(() => {
    if (!data || !data.length)
      return;

    const purified_data = _reviseData(data[0]['date'], data[data.length - 1]['date']);
    _setPurifiedData(purified_data);
  }, [data, _reviseData]);


  if (!_purified_data || !_purified_data.length)
    return (
      <div className="text-center py-4 text-light" style={{background: '#EDD'}}>
        <h2>NO DATA</h2>
      </div>
    );


  function _zoom() {
    let { left, right } = _viewPort;

    if (left === right || right === '') {
      _setViewPort({
        ..._viewPort,
        left: '',
        right: '',
      });
      return;
    }

    // xAxis domain
    if (left > right) [left, right] = [right, left];
    
    const purified_data = _reviseData(left, right);

    _setViewPort({
      left: '',
      right: '',
    });

    _setPurifiedData(purified_data);
  }

  function _zoomOut() {
    const purified_data = _reviseData(data[0]['date'], data[data.length - 1]['date']);
    _setViewPort({
      left: '',
      right: ''
    });
    _setPurifiedData(purified_data);
  }

  const AssetRatioTooltip = ({ active, payload, label }) => {
    if (!active || !payload || !payload.length)
      return null;

    // const date = moment(label).format('YY.MM.DD');
    return (
      <div className="bg-light p-2 border rounded" style={{fontSize: `${12}px`, whiteSpace: "pre"}}>
        <p className="fs-5 fw-bold text-center">{label}</p>
        <table>
          <tbody>
            <tr><td colSpan={3}><hr className="my-2" /></td></tr>
            {payload.map(datum => {
              let percent = ((100 * datum['value']) / datum['payload']['Total']).toFixed(2).padStart(5, ' ');
              return (
                <tr key={datum['name']}>
                  <td><p style={{ color: datum.color }}>{`${datum.name}`}</p></td>
                  <td><p style={{ color: datum.color }}>{` :  `}</p></td>
                  <td><code className="w-100" style={{ color: datum.color, textAlign: "end", fontSize: `${14}px` }}>
                    {`${UtilConverter.number2commadStringWithIntl(datum['value']).padStart(14, ' ')} (${percent}%)`}
                  </code></td>
                </tr>
              );                  
            }).reverse()}
            <tr><td colSpan={3}><hr className="my-2" /></td></tr>
            <tr>
              <td><p className="fw-bolder" style={{ color: 'black' }}>{`Total`}</p></td>
              <td><p style={{ color: 'black' }}>{` :  `}</p></td>
              <td className="text-end"><code className="text-dark fw-bolder" style={{ textAlign: "end", fontSize: `${14}px` }}>
                {` ${UtilConverter.number2commadStringWithIntl(payload[0]['payload']['Total'])}`}
              </code></td>
            </tr>
          </tbody>
        </table>
      </div>
    );
  };


  
  const renderIntermittentLabel = (props) => {
    const { x, y, offset, value, index, fontSize, fontWeight } = props;

    if (index % 2 === 0)
      return (
        <g>
          <text x={x} y={y - offset} textAnchor="middle" fontSize={fontSize} fontWeight={fontWeight}>
            {UtilConverter.number2shortenString(value)}
          </text>
        </g>
      );
    
    return (
      <g></g>
    );
  };


  return ( 
    <div style={{ userSelect: 'none' }}>
      <ResponsiveContainer width="100%" height={600}>
        <AreaChart
          width={500}
          height={400}
          data={_purified_data}
          defaultShowTooltip={true}
          margin={{
            top: 20,
            right: 30,
            left: 30,
            bottom: 50,
          }}
          onMouseDown={(e) => e && (_viewPort.left !== e.activeLabel) && _setViewPort({ ..._viewPort, left: e.activeLabel })}
          onMouseMove={(e) => e && _viewPort.left && (_viewPort.right !== e.activeLabel) && _setViewPort({ ..._viewPort, right: e.activeLabel })}
          onMouseUp={e => {
            if (_viewPort['left'] !== '' && _viewPort['right'] === '')
              _zoomOut();
            else
              _zoom();
          }}
        >
          <CartesianGrid strokeDasharray="1 2" />
          <XAxis dataKey="date"
            fontSize={10}
            angle={-40}
            textAnchor="end"
            // verticalAnchor="middle"
            tickSize={4}
            tickMargin={8}
            padding={{ left: 50, right: 50 }}
            // tickFormatter={(date) => {
            //   if (date instanceof Date)
            //     return moment(date).format('YY.MM.DD');
            //   return date;
            // }}
          />
          <YAxis
            allowDataOverflow 
            domain={[0, dataMax => parseInt(dataMax * 1.1)]}
            fontSize={10} 
            tickFormatter={(price) => UtilConverter.number2commadStringWithIntl(price)}
          />
          <Tooltip content={<AssetRatioTooltip />} />
          <Legend align="left" verticalAlign="top" iconSize={20} wrapperStyle={{top: 0}} />
          <Area type="monotone" dataKey={"Won"      } stackId="1" stroke={_colors_map["Won"      ]} fill={_colors_map["Won"      ]} animationDuration={300}>{_purified_data != null && _purified_data.length > 25 ? null : <LabelList dataKey={"PERCENT_Won"      } position="insideTopLeft" offset={5} fontSize={10} formatter={(v) => { if (v > 2) return v.toFixed(2)}}/>} </Area>
          <Area type="monotone" dataKey={"Dollar"   } stackId="1" stroke={_colors_map["Dollar"   ]} fill={_colors_map["Dollar"   ]} animationDuration={300}>{_purified_data != null && _purified_data.length > 25 ? null : <LabelList dataKey={"PERCENT_Dollar"   } position="insideTopLeft" offset={5} fontSize={10} formatter={(v) => { if (v > 2) return v.toFixed(2)}}/>} </Area>
          <Area type="monotone" dataKey={"KOSPI"    } stackId="1" stroke={_colors_map["KOSPI"    ]} fill={_colors_map["KOSPI"    ]} animationDuration={300}>{_purified_data != null && _purified_data.length > 25 ? null : <LabelList dataKey={"PERCENT_KOSPI"    } position="insideTopLeft" offset={5} fontSize={10} formatter={(v) => { if (v > 2) return v.toFixed(2)}}/>} </Area>
          <Area type="monotone" dataKey={"S&P500"   } stackId="1" stroke={_colors_map["S&P500"   ]} fill={_colors_map["S&P500"   ]} animationDuration={300}>{_purified_data != null && _purified_data.length > 25 ? null : <LabelList dataKey={"PERCENT_S&P500"   } position="insideTopLeft" offset={5} fontSize={10} formatter={(v) => { if (v > 2) return v.toFixed(2)}}/>} </Area>
          <Area type="monotone" dataKey={"NASDAQ"   } stackId="1" stroke={_colors_map["NASDAQ"   ]} fill={_colors_map["NASDAQ"   ]} animationDuration={300}>{_purified_data != null && _purified_data.length > 25 ? null : <LabelList dataKey={"PERCENT_NASDAQ"   } position="insideTopLeft" offset={5} fontSize={10} formatter={(v) => { if (v > 2) return v.toFixed(2)}}/>} </Area>
          <Area type="monotone" dataKey={"Luxuary"  } stackId="1" stroke={_colors_map["Luxuary"  ]} fill={_colors_map["Luxuary"  ]} animationDuration={300}>{_purified_data != null && _purified_data.length > 25 ? null : <LabelList dataKey={"PERCENT_Luxuary"  } position="insideTopLeft" offset={5} fontSize={10} formatter={(v) => { if (v > 2) return v.toFixed(2)}}/>} </Area>
          <Area type="monotone" dataKey={"Stocks-KR"} stackId="1" stroke={_colors_map["Stocks-KR"]} fill={_colors_map["Stocks-KR"]} animationDuration={300}>{_purified_data != null && _purified_data.length > 25 ? null : <LabelList dataKey={"PERCENT_Stocks-KR"} position="insideTopLeft" offset={5} fontSize={10} formatter={(v) => { if (v > 2) return v.toFixed(2)}}/>} </Area>
          <Area type="monotone" dataKey={"Stocks-US"} stackId="1" stroke={_colors_map["Stocks-US"]} fill={_colors_map["Stocks-US"]} animationDuration={300}>{_purified_data != null && _purified_data.length > 25 ? null : <LabelList dataKey={"PERCENT_Stocks-US"} position="insideTopLeft" offset={5} fontSize={10} formatter={(v) => { if (v > 2) return v.toFixed(2)}}/>} </Area>
          <Area type="monotone" dataKey={"Reits"    } stackId="1" stroke={_colors_map["Reits"    ]} fill={_colors_map["Reits"    ]} animationDuration={300}>{_purified_data != null && _purified_data.length > 25 ? null : <LabelList dataKey={"PERCENT_Reits"    } position="insideTopLeft" offset={5} fontSize={10} formatter={(v) => { if (v > 2) return v.toFixed(2)}}/>} </Area>
          <Area type="monotone" dataKey={"T-bond_I" } stackId="1" stroke={_colors_map["T-bond_I" ]} fill={_colors_map["T-bond_I" ]} animationDuration={300}>{_purified_data != null && _purified_data.length > 25 ? null : <LabelList dataKey={"PERCENT_T-bond_I" } position="insideTopLeft" offset={5} fontSize={10} formatter={(v) => { if (v > 2) return v.toFixed(2)}}/>} </Area>
          <Area type="monotone" dataKey={"P-Metal"  } stackId="1" stroke={_colors_map["P-Metal"  ]} fill={_colors_map["P-Metal"  ]} animationDuration={300}>{_purified_data != null && _purified_data.length > 25 ? null : <LabelList dataKey={"PERCENT_P-Metal"  } position="insideTopLeft" offset={5} fontSize={10} formatter={(v) => { if (v > 2) return v.toFixed(2)}}/>}            
            {_purified_data != null && _purified_data.length > 25
              ? (
                  <LabelList dataKey={"Total"} position="top" offset={14} fontSize={10}
                    formatter={(datum) => {
                      return UtilConverter.number2shortenString(datum);
                    }}
                    content={renderIntermittentLabel}
                  />
                )
              : (
                  <LabelList dataKey={"Total"} position="top" offset={14} fontSize={10}
                    formatter={(datum) => {
                      return UtilConverter.number2commadStringWithIntl(datum);
                    }}
                    content={renderIntermittentLabel}
                  />  
                )
            }            
          </Area>
          {_viewPort['left'] && _viewPort['right'] ? (
            <ReferenceArea x1={_viewPort['left']} x2={_viewPort['right']} strokeOpacity={0.3} />
          ) : null}
        </AreaChart>
      </ResponsiveContainer>
    </div>
  );
}
 
export default RechartTotalAsstRatio;