import React from 'react';
import moment from 'moment';
import xlsx from 'xlsx';
import { UtilSheet } from '../../utils';
import RechartDayTrade3min from './recharts/rechart_day_trade_3min';


const VDayTrades = () => {
  const [_data, _setData] = React.useState({});
  const _refDayTradeFileForm = React.useRef();

  const _parseData = React.useCallback((raw_data) => {
    var data = {};
    var data_pre = {};

    Object.keys(raw_data).forEach((sheetName) => {      
      if (
        sheetName === 'KODEX ' || 
        sheetName === '  S&P500  ETN' || 
        sheetName === 'TRUE   100 ETN' || 
        sheetName === 'HANARO S&P()'
      ) {
        raw_data[sheetName].forEach((row) => {
          let date = new Date(row['']);
          if (date.getMinutes() % 3 > 0)
            date.setMinutes(date.getMinutes() - date.getMinutes() % 3);          
          let date_string = moment(date).format('YY.MM.DD HH:mm');
          let name = sheetName;

          if (!data_pre[date_string])
            data_pre[date_string] = {};

          if (!data_pre[date_string][name])
            data_pre[date_string][name] = {};

          data_pre[date_string][name]['price'] = row[''];
          data_pre[date_string][name]['rsi'] = parseFloat((row['RSI'] * 100).toFixed(2));
        });
      } else if (sheetName === '') {
        raw_data[sheetName].forEach((row) => {
          let date = UtilSheet.serialDateToJSDate(row[''], row['']);          
          if (date.getMinutes() % 3 > 0)
            date.setMinutes(date.getMinutes() - date.getMinutes() % 3);          
          let date_string = moment(date).format('YY.MM.DD HH:mm');
          let name = row[''];

          if (!data_pre[date_string])
            data_pre[date_string] = {};

          if (!data_pre[date_string][name])
            data_pre[date_string][name] = {};

          if (row[''] === '') {
            data_pre[date_string][name]['sell_price' ] = row[''];
            data_pre[date_string][name]['sell_volume'] = row[''];
          } else if (row[''] === '') {
            data_pre[date_string][name]['buy_price' ] = row[''];
            data_pre[date_string][name]['buy_volume'] = row[''];
          }
        })
      }
    });
    
    let date_keys = Object.keys(data_pre);
    date_keys.sort();

    date_keys.forEach((date_key, i, arr) => {
      let names = Object.keys(data_pre[date_key]);
      names.forEach((name) => {
        if (!data[name])
          data[name] = [];

        data[name].push({
          date_time: date_key,
          ...data_pre[date_key][name],
        });
      });
    });

    Object.keys(data).forEach((name) => {
      var last_price = 0;
      var last_rsi = 0;
      data[name] = data[name].map((datum, i, arr) => {
        let price = datum['price'] || last_price;
        let rsi = datum['rsi'] || last_rsi;
        last_price = price;
        last_rsi = rsi;
  
        return {
          ...datum,
          price,
          rsi,
        };
      });
    });

    return data;
  }, []);

  
  function uploadSheet(event) {
    var input = event.target; 
    var reader = new FileReader();
    reader.onload = function() { 
      var fdata = reader.result; 
      var read_buffer = xlsx.read(fdata, {type : 'binary'}); 

      let data = {};
      read_buffer.SheetNames.forEach(function(sheetName) {
        var data_rows = xlsx.utils.sheet_to_json(read_buffer.Sheets[sheetName]); 
        data[sheetName] = data_rows;
      });
      
      _refDayTradeFileForm.current.reset();      
      localStorage.setItem('day_trade_data', JSON.stringify(data));
      
      _setData(_parseData(data));
    }; 
    reader.readAsBinaryString(input.files[0]); 
  }


  return (
    <div>
      <div className="text-start fw-bold my-5 px-4" style={{fontSize: `calc(${2}rem + ${2}vw)`}}>
        DAY TRADES
      </div>

      <div className="mt-5 d-flex justify-content-center">
        <div className="pe-2">
          <label type="button" className="btn btn-secondary"
            onClick={e => window.open('https://docs.google.com/uc?export=download&id=1odYcI7b41XpWwZUs--q41rjhw7plenW6', '_blank')}
          >
            DOWNLOAD SAMPLE
          </label>
        </div>
        <div>
          <form ref={_refDayTradeFileForm}>
            <input type="file" accept=".xlsx" id="pricesFileInput"
              className="d-none"
              onClick={e => e.target.key = 'prices' + new Date().getMilliseconds()}
              // name="pricesFile" onChange={_setPrices}
              name="pricesFile" onChange={uploadSheet}
            />
          </form>
          <label type="button" htmlFor="pricesFileInput" className="btn btn-danger">UPLOAD XLSX</label>
        </div>
      </div>

      {/* {Object.keys(_data).map((name) => { */}
      { [
          'KODEX ',
          '  S&P500  ETN',
          'TRUE   100 ETN',
          'HANARO S&P()',
        ].map((name) => {
        return (
          <React.Fragment key={name}>
            <div className="mt-5 mb-3 px-4 py-5 bg-dark text-light">
              <h1><b>{name}</b></h1>
            </div>

            <div className="mt-4">
              <RechartDayTrade3min data={_data[name]} />
            </div>
          </React.Fragment>
        );
      })}
    </div>
  );
}

export default VDayTrades;