import moment from "moment";
import React from "react";
import xlsx from 'xlsx';
import { UtilSheet } from "../../utils";
import RechartAlpha from "./recharts/rechart_alpha";
import RechartArbitrage from "./recharts/rechart_arbitrage";
import RechartDayTrade from "./recharts/rechart_day_trade";
import RechartTotalAsstRatio from "./recharts/rechart_total_asset";

const VTradeHistory = () => {
  const [_total_asset_data                  , setTotalAssetData                ] = React.useState([]);  
  const [_algorithm_trade_data              , setAlgorithmTradeData            ] = React.useState({});
  const [_alpha_data                        , setAlphaData                     ] = React.useState({});
  const [_selected_algorithm_trade_data_name, setSelectedAlgorithmTradeDataName] = React.useState('KOSPI');
  const [_arbitrage_data                    , setArbitrageData                 ] = React.useState([]);
  
  let _selected_algorithm_trade_data = _algorithm_trade_data[_selected_algorithm_trade_data_name] || [];

  const _refPricesFileForm = React.useRef();

  const _parseData = React.useCallback((data) => {
    var total_asset_data = [];
    var algorithm_trade_data = {};
    let arbitrage_data_pre = {};
    let arbitrage_data = [];

    let alpha_data_pre = {};
    let alpha_data = [];


    Object.keys(data).forEach((sheetName) => {
      if (sheetName === 'Total') {
        data[sheetName].forEach((row) => {
          var date = UtilSheet.serialDateToJSDate(row['']);
          var date_string = moment(date).format('YY.MM.DD');
          delete row[''];
          
          // for alpha_data_pre          
          if (!alpha_data_pre[date_string])
            alpha_data_pre[date_string] = {};
          alpha_data_pre[date_string]['return_of_asset'] = parseFloat((row['c_return'] || 0).toFixed(2));
          alpha_data_pre[date_string]['memo'] = row['memo'];

          let safety_asset_ratio = (parseFloat(row['cash_ratio']) * 100).toFixed(2);
          alpha_data_pre[date_string]['safety_asset_ratio'] = safety_asset_ratio;


          function getPercentage(key) {
            return parseFloat(((100 * row[key]) / row['Total']).toFixed(2));
          }
          
          var percentages = {};
          [
            "Won"      ,
            "Dollar"   ,
            "KOSPI"    , 
            "S&P500"   ,
            "NASDAQ"   ,
            "Luxuary"  ,
            "Reits"    ,
            "Stocks-KR",
            "Stocks-US",
            "T-bond_I" ,
            "P-Metal"
          ].forEach((key) => {
            percentages[`PERCENT_${key}`] = getPercentage(key)
          });

          total_asset_data.push({
            ...row,
            ...percentages,
            date: date_string,
          });
        });

      } else if (sheetName === 'KOSPI' || sheetName === 'S&P500' || sheetName === 'NASDAQ' || sheetName === 'LUXUARY') {

        algorithm_trade_data[sheetName] = data[sheetName].map((row) => {
          var date = UtilSheet.serialDateToJSDate(row['']);
          var date_string = moment(date).format('YY.MM.DD');

          // for arbitrage_data_pre
          if (!arbitrage_data_pre[date_string])
           arbitrage_data_pre[date_string] = { 'total': 0 };
          if (row[''] && row['']) {
            let min_traded_volume = Math.min(row[''], row['']);
            let arbitrage = (row[''] - row['']) * min_traded_volume;
            arbitrage_data_pre[date_string][sheetName] = arbitrage;
            arbitrage_data_pre[date_string]['total'] += arbitrage;
          }

          // for alpha_data_pre          
          if (sheetName === 'KOSPI' || sheetName === 'S&P500' || sheetName === 'NASDAQ') {
            if (!alpha_data_pre[date_string])
              alpha_data_pre[date_string] = {};
            alpha_data_pre[date_string][sheetName] = row['x1']; // multiple x1 prices
          }

          return {
            date: date_string,
            price: row[''],
            buy_price: row[''],
            sell_price: row[''],
            buy_volume: row[''],
            sell_volume: row[''],
            a_price: row[''],
            count: row[''],
          };
        });
      } else if (sheetName === 'day_trade') {
        data[sheetName].forEach((row) => {
          var date_string = moment(UtilSheet.serialDateToJSDate(row['date'])).format('YY.MM.DD');
          // for alpha_data_pre
          if (!alpha_data_pre[date_string])
            alpha_data_pre[date_string] = {};
          alpha_data_pre[date_string][sheetName] = parseFloat(row['return'].toFixed(2));
        });
      } else if (sheetName === 'Global_Index') {
        data[sheetName].forEach((row) => {
          var date = UtilSheet.serialDateToJSDate(row['']);
          var date_string = moment(date).format('YY.MM.DD');
          if (!alpha_data_pre[date_string])
            alpha_data_pre[date_string] = {};
          alpha_data_pre[date_string][sheetName] = row['']; // multiple x1 prices        
        });
      }
    });


    // arbitrage_data_pre => arbitrage_data
    let accumulation = 0;
    Object.keys(arbitrage_data_pre).forEach((date, i) => {
      accumulation += arbitrage_data_pre[date]['total'];
      arbitrage_data.push({
        date,
        ...arbitrage_data_pre[date],
        accumulation
      });
    });

    // alpha_data_pre => alpha_data
    Object.keys(alpha_data_pre).forEach((date, i) => {
      alpha_data.push({
        date,
        ...alpha_data_pre[date]
      });
    });

    return {
      total_asset_data,
      algorithm_trade_data,
      arbitrage_data,
      alpha_data,
    };
  }, []);

  React.useEffect(() => {
    let local_data = localStorage.getItem('last_finance_data');
    if (!local_data) 
      return;

    var data = JSON.parse(local_data);
    try {
      const { total_asset_data, algorithm_trade_data, arbitrage_data, alpha_data } = _parseData(data);
      setTotalAssetData(total_asset_data);
      setAlgorithmTradeData(algorithm_trade_data);
      setArbitrageData(arbitrage_data);
      setAlphaData(alpha_data);
    } catch (e) {
      localStorage.removeItem('last_finance_data');
      window.location.reload();
    }
  }, [_parseData]);


  /** CSV parse example  */
  // function _setPrices(e) {   
  //   var reader = new FileReader();
  //   reader.onload = function(e) {
  //     // Use reader.result
  //     parseCSV(reader.result, {}, function (err, output) {
  //       console.log(output);
  //     });
  //   }
  //   reader.readAsText(e.target.files[0]);
  // }
  /**  */

  function uploadSheet(event) {
    var input = event.target; 
    var reader = new FileReader();
    reader.onload = function() { 
      var fdata = reader.result; 
      var read_buffer = xlsx.read(fdata, {type : 'binary'}); 

      let data = {};
      read_buffer.SheetNames.forEach(function(sheetName) {
        var data_rows = xlsx.utils.sheet_to_json(read_buffer.Sheets[sheetName]); 
        data[sheetName] = data_rows;
      });
      
      _refPricesFileForm.current.reset();      
      localStorage.setItem('last_finance_data', JSON.stringify(data));
      const { total_asset_data, algorithm_trade_data, arbitrage_data, alpha_data } = _parseData(data);
      setTotalAssetData(total_asset_data);
      setAlgorithmTradeData(algorithm_trade_data);
      setArbitrageData(arbitrage_data);
      setAlphaData(alpha_data);
    }; 
    reader.readAsBinaryString(input.files[0]); 
  }

  return ( 
    <div>
      <div className="text-start fw-bold my-5 px-4 w-100" style={{fontSize: `calc(${2}rem + ${2}vw)`}}>
        TRADE HISTORY
      </div>
      <div className="mt-5 d-flex justify-content-center w-100">
        <div className="pe-2">
          <label type="button" className="btn btn-secondary" 
            onClick={e => window.open('https://docs.google.com/uc?export=download&id=YourIndividualID', '_blank')}
          >
            DOWNLOAD SAMPLE
          </label>
        </div>
        <div>
          <form ref={_refPricesFileForm}>
            <input type="file" accept=".xlsx" id="pricesFileInput"
              className="d-none"
              onClick={e => e.target.key = 'prices' + new Date().getMilliseconds()}
              // name="pricesFile" onChange={_setPrices}
              name="pricesFile" onChange={uploadSheet}
            />
          </form>
          <label type="button" htmlFor="pricesFileInput" className="btn btn-danger">UPLOAD XLSX</label>
        </div>
      </div>

   
      <div className="mt-5 mb-3 px-4 py-5 bg-dark text-light">
        <h1><b>ALGORITHM TRADE</b></h1>
      </div>   
      <div className="mt-5 px-3 fw-bold">Select data</div>
      <div className="px-3 d-flex flex-wrap">
        {Object.keys(_algorithm_trade_data).map((key, i) => {
          return (
            <div key={i} className="form-check px-3 py-2">
              <input
                className="form-check-input" type="radio" name={`radio_${key}`} id={`radio_${key}`}
                checked={_selected_algorithm_trade_data_name === key} onChange={e => setSelectedAlgorithmTradeDataName(key)}
              />
              <label className="form-check-label" htmlFor={`radio_${key}`}>
                {key}
              </label>
            </div>
          );
        })}
      </div>

      <div className="mt-4">
        <RechartDayTrade data={_selected_algorithm_trade_data} />
      </div>
      <div className="mt-5">
        <RechartArbitrage data={_arbitrage_data} />
      </div>

      <div className="mt-5 mb-3 px-4 py-5 bg-dark text-light">
        <h1><b>TOTAL ASSET RATIO</b></h1>
      </div>
      <div className="mt-5">
        <RechartTotalAsstRatio data={_total_asset_data} />
      </div>

      <div className="mt-5 mb-3 px-4 py-5 bg-dark text-light">
        <h1><b>ALPHA</b></h1>
      </div>
      <div className="mt-5">
        <RechartAlpha data={_alpha_data} />
      </div>
    </div>
  );
}
 
export default VTradeHistory;